import { useCallback } from 'react';

import classNames from 'classnames';
import './index.scss';

export type LogoProps = {
  corporate?: string;
  hasImage?: boolean;
  size?: 'sm' | 'md' | 'lg';
};

const SIZE_MPAS = {
  sm: 'text-[1rem]',
  md: 'text-[1.25rem]',
  lg: 'text-[1.625rem]'
};

const Logo = ({ corporate = 'QUARTERBACK', hasImage = false, size = 'md' }: LogoProps) => {
  const setLowercase = useCallback(() => {
    return corporate.toLowerCase();
  }, [corporate]);

  return (
    <h1 className="logo">
      {hasImage ? (
        <img src={`/assets/images/ci/ci-${setLowercase()}.png`} alt="ci" className="logo__image" />
      ) : (
        <span className={classNames('logo__label', SIZE_MPAS[size])}>{corporate}</span>
      )}
    </h1>
  );
};

export default Logo;
