import { ButtonHTMLAttributes, ReactNode } from 'react';

import classNames from 'classnames';
import './index.scss';

export type ButtonProps = {
  flex?: 'none' | 1 | 2 | 3;
  variant?: 'default' | 'ghost' | 'link' | 'outline';
  size?: 'xs' | 'sm' | 'md' | 'lg';
  color?: 'primary' | 'tertiary';
  full?: boolean;
  loaded?: boolean;
  prefix?: ReactNode;
  suffix?: ReactNode;
  animated?: boolean;
  className?: string;
} & ButtonHTMLAttributes<HTMLButtonElement>;

const SIZE_MAPS = {
  lg: 'button--lg',
  md: 'button--md',
  sm: 'button--sm',
  xs: 'button--xs'
};

const COLOR_MAPS = {
  primary: 'button--primary',
  tertiary: 'button--tertiary'
};

const FLEX_MAPS = {
  none: '',
  1: 'button--flex-1',
  2: 'button--flex-2',
  3: 'button--flex-3'
};

const Button = ({
  type = 'button',
  children,
  disabled,
  flex = 'none',
  variant = 'default',
  size = 'md',
  color = 'primary',
  full = false,
  loaded = true,
  prefix,
  suffix,
  animated = false,
  className,
  onClick
}: ButtonProps) => {
  const VARIANT_BUTTON = (variant: string) => {
    switch (variant) {
      case 'ghost':
        return `button--ghost ${SIZE_MAPS[size]}`;
      case 'link':
        return `button--link ${SIZE_MAPS[size]}`;
      case 'outline':
        return `button--outline ${SIZE_MAPS[size]} ${COLOR_MAPS[color]}`;
      case 'default':
      default:
        return `${SIZE_MAPS[size]} ${COLOR_MAPS[color]}`;
    }
  };

  return (
    <button
      type={type}
      className={classNames(
        'button',
        FLEX_MAPS[flex],
        VARIANT_BUTTON(variant),
        {
          'button--full-width': full,
          'button--no-animation': !animated
        },
        className
      )}
      disabled={disabled}
      onClick={onClick}
    >
      {!loaded && <span className="loading loading-spinner" />}
      {prefix}
      {children}
      {suffix}
    </button>
  );
};

export default Button;
