import { ReactNode } from 'react';

import { Typography } from '..';

import classNames from 'classnames';

export type ChipProps = {
  variant?: 'filled' | 'outlined' | 'hashtag';
  color?: 'red' | 'green' | 'blue';
  label: ReactNode;
  onClick?: () => void;
};

const BACKGROUND_MAPS = {
  red: 'bg-[#FFF1F1]',
  green: 'bg-[#DDFFF6]',
  blue: 'bg-primary-100'
};

const BORDER_MAPS = {
  red: 'border-[#FFF1F1]',
  green: 'border-[#DDFFF6]',
  blue: 'border-primary-100'
};

const COLOR_MAPS = {
  red: 'text-[#FF5247]',
  green: 'text-[#2FA989]',
  blue: 'text-primary-700'
};

const Chip = ({ variant = 'filled', color = 'green', label, onClick }: ChipProps) => {
  const CustomTag = onClick ? 'button' : 'div';

  return (
    <CustomTag
      {...(onClick && { type: 'button' })}
      className={classNames(
        'qb-chip inline-flex items-center',
        { 'py-1 px-3 rounded border': variant !== 'hashtag' },
        variant === 'filled' ? BACKGROUND_MAPS[color] : 'bg-transparent',
        variant === 'outlined' ? BORDER_MAPS[color] : 'border-transparent'
      )}
      onClick={onClick}
    >
      {variant === 'hashtag' && (
        <Typography variant="head" size="xs" color="text-gray-400" className="mr-[0.125rem]">
          #
        </Typography>
      )}
      <Typography
        variant={variant === 'hashtag' ? 'body' : 'head'}
        size="xs"
        className={variant !== 'hashtag' ? COLOR_MAPS[color] : 'text-gray-700'}
      >
        {label}
      </Typography>
    </CustomTag>
  );
};

export default Chip;
