import { HTMLAttributes, ReactNode } from 'react';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

type HeadSize = 'xl' | 'lg' | 'base' | 'sm' | 'xs';
type BodySize = 'lg' | 'base' | 'sm' | 'xs';

export type TypographyProps = {
  variant?: 'head' | 'body';
  size?: HeadSize | BodySize;
  align?: 'left' | 'center' | 'right';
  color?: string;
  children: ReactNode;
} & HTMLAttributes<HTMLBaseElement>;

const HEAD_SIZE_MAPS: Record<HeadSize, string> = {
  xl: 'text-head-xl',
  lg: 'text-head-lg',
  base: 'text-head-base',
  sm: 'text-head-sm',
  xs: 'text-head-xs'
};

const BODY_SIZE_MAPS: Record<BodySize, string> = {
  lg: 'text-body-lg',
  base: 'text-body-base',
  sm: 'text-body-sm',
  xs: 'text-body-xs'
};

const ALIGN_MAPS = {
  left: 'text--left',
  center: 'text--center',
  right: 'text--right'
};

const Typography = ({
  variant = 'body',
  size = 'base',
  align = 'left',
  color,
  children,
  className
}: TypographyProps) => {
  return (
    <>
      {variant === 'head' ? (
        <strong className={classNames(HEAD_SIZE_MAPS[size], ALIGN_MAPS[align], color, className)}>
          {children}
        </strong>
      ) : (
        <span
          className={classNames(
            BODY_SIZE_MAPS[size as BodySize],
            ALIGN_MAPS[align],
            color,
            className
          )}
        >
          {children}
        </span>
      )}
    </>
  );
};

export default Typography;
