import { ReactNode } from 'react';

import Icon from '../Icon';

import classNames from 'classnames';
import './index.scss';
// ---------------------------------------------------------------------- //

export type CardProps = {
  children: ReactNode;
  backgroundColor?: string;
  alignItems?: 'start' | 'center' | 'end';
  hasButton?: boolean;
  onClose?: () => void;
  py?: string;
  pt?: string;
  pb?: string;
  px?: string;
  className?: string;
};

const ALIGN_MAPS = {
  start: 'qb-card--items-start',
  center: 'qb-card--items-center',
  end: 'qb-card--items-end'
};

const Card = ({
  children,
  backgroundColor,
  alignItems = 'start',
  hasButton = false,
  onClose,
  pt = 'pt-10',
  pb = 'pb-[3.125rem]',
  py,
  px = 'px-[3.125rem]',
  className
}: CardProps) => {
  return (
    <div
      className={classNames(
        'qb-card',
        backgroundColor ? backgroundColor : 'qb-card--white',
        ALIGN_MAPS[alignItems],
        !py && pt ? pt : '',
        !py && pb ? pb : '',
        py,
        px,
        className
      )}
    >
      {hasButton && (
        <div className="qb-card__close-area">
          <Icon name="icon-basic-close" onClick={onClose} className="qb-card__close-icon" />
        </div>
      )}
      {children}
    </div>
  );
};

export default Card;
