import { useState } from 'react';
import {
  ColumnDef,
  OnChangeFn,
  RowSelectionState,
  SortingState,
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  useReactTable
} from '@tanstack/react-table';
import _map from 'lodash/map';
import _range from 'lodash/range';

import { Icon } from 'components/ui';

import classNames from 'classnames';
import './index.scss';
// ----------------------------------------------------------------------
interface Props {
  data: unknown[];
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  columns: ColumnDef<any, any>[];
  usePagination?: boolean;
  rowSelection?: RowSelectionState;
  setRowSelection?: OnChangeFn<RowSelectionState>;
}
// ----------------------------------------------------------------------
// NOTE USE TanStack Table(https://tanstack.com/)
export default function Table(props: Props) {
  const { data, columns, usePagination = true, rowSelection, setRowSelection } = props;
  const [sorting, setSorting] = useState<SortingState>([]);

  const table = useReactTable({
    data,
    columns,
    state: {
      sorting,
      rowSelection
    },
    onRowSelectionChange: setRowSelection,
    getCoreRowModel: getCoreRowModel(),
    // sort
    onSortingChange: setSorting,
    getSortedRowModel: getSortedRowModel(),
    // pagination
    getFilteredRowModel: getFilteredRowModel(),
    getPaginationRowModel: usePagination ? getPaginationRowModel() : undefined,
    debugTable: false,
    manualSorting: true
  });

  return (
    <div className="w-full">
      <table className="table">
        <thead>
          {_map(table.getHeaderGroups(), (headerGroup) => (
            <tr key={headerGroup.id} className="table__header__tr">
              {_map(headerGroup.headers, (header, index) => (
                <th
                  key={header.id}
                  colSpan={header.colSpan}
                  className={classNames('table__header__tr__th', {
                    '--first': index === 0
                  })}
                >
                  <div
                    {...{
                      className: header.column.getCanSort() ? 'cursor-pointer select-none' : '',
                      onClick: header.column.getToggleSortingHandler()
                    }}
                  >
                    {header.isPlaceholder
                      ? null
                      : flexRender(header.column.columnDef.header, header.getContext())}
                    {{
                      asc: <Icon name="icon-chevron-up-small" />,
                      desc: <Icon name="icon-chevron-down-small" />
                    }[header.column.getIsSorted() as string] ?? null}
                  </div>
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {_map(table.getRowModel().rows, (row) => (
            <tr key={row.id} className="table__body__tr">
              {row.getVisibleCells().map((cell, index) => (
                <td
                  key={cell.id}
                  className={classNames('table__body__tr__td', {
                    '--first': index === 0
                  })}
                >
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
      {/* Pagination */}
      {usePagination && (
        <div className="pagination">
          <button
            className="pagination__button"
            onClick={() => table.previousPage()}
            disabled={!table.getCanPreviousPage()}
          >
            {'<'}
          </button>
          {_map(_range(0, 3), (index) => (
            <button
              key={index}
              className={classNames('pagination__button --page', {
                '--active': index === 0
              })}
              onClick={() => table.setPageIndex(table.getState().pagination.pageIndex + index)}
            >
              {table.getState().pagination.pageIndex + index + 1}
            </button>
          ))}
          <div className="pagination__skip">...</div>
          {_map(_range(3, 0), (index) => (
            <button
              key={index}
              className="pagination__button --page"
              onClick={() => table.setPageIndex(table.getPageCount() - index)}
            >
              {table.getPageCount() - index + 1}
            </button>
          ))}
          <button
            className="pagination__button"
            onClick={() => table.nextPage()}
            disabled={!table.getCanNextPage()}
          >
            {'>'}
          </button>
        </div>
      )}
    </div>
  );
}
