import { Touchable } from 'components/ui';

import classNames from 'classnames';
// ----------------------------------------------------------------------

export type IconProps = {
  name: IconTypes;
  size?: string;
  color?: string;
  onClick?: () => void;
  className?: string;
};

const Icon = ({
  name,
  size = 'text-[1.5rem]',
  color = 'text-gray-800',
  onClick,
  className
}: IconProps) => {
  return (
    <>
      {onClick ? (
        <Touchable onClick={onClick} className={className}>
          <i className={classNames('icon', name, size, color)} />
        </Touchable>
      ) : (
        <i className={classNames('icon', name, size, color, className)} />
      )}
    </>
  );
};

export default Icon;
