import { ReactNode } from 'react';

import { Typography } from 'components/ui';
import type { TypographyProps } from 'components/ui/Typography';

import classNames from 'classnames';
import './index.scss';
// ----------------------------------------------------------------------

export type PageTitleProps = {
  flexDirection?: 'row' | 'column';
  title?: ReactNode;
  titleSize?: TypographyProps['size'];
  description?: ReactNode;
  hasSeparator?: boolean;
};

const FLEX_DIRECTION_MAPS = {
  row: 'qb-page-title--row',
  column: 'qb-page-title--column'
};

const PageTitle = ({
  flexDirection = 'row',
  title,
  titleSize = 'xl',
  description,
  hasSeparator = false
}: PageTitleProps) => {
  return (
    <div
      className={classNames('qb-page-title', FLEX_DIRECTION_MAPS[flexDirection], {
        'qb-page-title--has-separator':
          flexDirection === 'row' && title && description && hasSeparator
      })}
    >
      {title && (
        <Typography variant="head" size={titleSize} className="qb-page-title">
          {title}
        </Typography>
      )}
      {description && (
        <Typography size="xs" className="qb-page-title__description">
          {description}
        </Typography>
      )}
    </div>
  );
};

export default PageTitle;
