import { ReactNode, useId } from 'react';
import ReactModal from 'react-modal';
import { motion, AnimatePresence } from 'framer-motion';

import { fadeAnimation } from 'components/animate/variants/fade';
import { scaleUpFadeAnimation } from 'components/animate/variants/scale';

import { Typography, ButtonGroup, Button, Icon } from 'components/ui';
import './index.scss';

export type ConfirmProps = {
  isOpen: boolean;
  contentLabel?: string;
  title?: ReactNode;
  children: ReactNode;
  onAfterOpen?: () => void;
  onAfterClose?: () => void;
  onClose?: () => void;
  closeLabel?: string;
  onSuccess?: () => void;
  successLabel?: string;
  shouldCloseOnOverlayClick?: boolean;
  hasCustomActions?: boolean;
};

const Confirm = ({
  isOpen = false,
  contentLabel,
  title,
  children,
  onAfterOpen,
  onAfterClose,
  onClose,
  closeLabel = '',
  onSuccess,
  successLabel = '',
  shouldCloseOnOverlayClick = false,
  hasCustomActions = false
}: ConfirmProps) => {
  const id = useId();

  return (
    <AnimatePresence>
      {isOpen && (
        <ReactModal
          id={`confirm${id}`}
          role="confirm"
          isOpen={isOpen}
          contentLabel={contentLabel || 'Confirm'}
          portalClassName="qb-confirm"
          ariaHideApp={false}
          onAfterOpen={onAfterOpen}
          onAfterClose={onAfterClose}
          shouldCloseOnOverlayClick={shouldCloseOnOverlayClick}
          overlayElement={() => (
            <motion.div className="qb-confirm__overlay" {...fadeAnimation}>
              <motion.div className="qb-confirm__container" {...scaleUpFadeAnimation}>
                <div className="qb-confirm__content">
                  {title && (
                    <div className="qb-confirm__header">
                      <Typography variant="head" className="qb-confirm__header-title">
                        {title}
                      </Typography>
                      {onClose && (
                        <div className="qb-confirm__header-closed-icon">
                          <Icon name="icon-basic-close" size="text-[1.25rem]" onClick={onClose} />
                        </div>
                      )}
                    </div>
                  )}
                  <div className="qb-confirm__body">{children}</div>
                </div>
                {!hasCustomActions && (
                  <div className="qb-confirm__actions">
                    <ButtonGroup>
                      {onClose && (
                        <Button flex={1} variant="outline" onClick={onClose}>
                          {closeLabel}
                        </Button>
                      )}
                      <Button flex={1} onClick={onSuccess}>
                        {successLabel}
                      </Button>
                    </ButtonGroup>
                  </div>
                )}
              </motion.div>
            </motion.div>
          )}
        />
      )}
    </AnimatePresence>
  );
};

export default Confirm;
